import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/login.vue'
const User = r =>require(['../components/user.vue'],r);
const Home = r =>require(['../components/home.vue'],r);

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/user',
      name: 'user',
      meta:{
        requireLogin:true
      },
      component: User
    },
    {
      path: '/home',
      name: 'home',
      meta:{
        requireLogin:true
      },
      component: Home
    }
  ]
})



