<template>
  <div class="hello">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm"  >
      <el-form-item label="用户名" prop="username">
        <el-input type="password" v-model="ruleForm.username" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="自动登录">
        <el-switch v-model="ruleForm.checked"></el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primcheckedary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      var username = (rule, value, callback) => {
        if(value === ''){
          callback(new Error("请输入用户名"));
        }else
          callback();
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          password: '',
          checked:true,
          username: ''
        },
        rules: {
          password: [
            { validator: validatePass, trigger: 'blur' }
            // { min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: 'blur' }
          ],
          username: [
            { validator: username, trigger: 'blur' }
            // { min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: 'blur' }
          ],
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.postHttp("login",this.ruleForm)
              .then(res =>{
                  if(res){
                      let data = res.data;
                      if(data.status == 200){
                         this.$store.commit("setLoginInfo",data.token);
                         this.$router.push({name:'home'});
                      }else{
                        this.$message.warning(data.msg);
                      }
                  }
              })
            return false;
          } else {
            this.$message.error('请填写正确的数据后再进行提交');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



