import Vue from 'vue';
import Axios from 'axios';
import {baseURL} from "../common/var";
import store from "../store";

Axios.defaults.baseURL = baseURL;

Axios.interceptors.request.use(config=> {
    // 显示loading
    if(store.getters.loginInfo != null){  //如果登录则在每次请求前加上Authorization 头
        config.headers.Token = store.getters.loginInfo;
    }
    return config
}, error =>{
    // 请求错误时弹框提示，或做些其他事
    return Promise.reject(error)
})

Axios.interceptors.response.use(function (response) {
    // 对响应数据做点什么，允许在数据返回客户端前，修改响应的数据
    // 如果只需要返回体中数据，则如下，如果需要全部，则 return response 即可
    return response
}, res => {
    switch(res.response.status){
        case 401:alert("没有权限");break;
        case 403:alert("禁止访问");break;
        case 404:alert("找到不到对应的资源");break;
        case 412:
            window.sessionStorage.removeItem("token");
            location.reload();
            break;
        case 500:alert("服务器繁忙");break;
    }
    // if(error)nsole.log(res);?{
    //     window.sessionStorage.removeItem("token");
    //     location.reload();
    // }

})

