function successState (res) {
    // 隐藏loading
    // 统一判断后端返回的错误码(错误码与后台协商而定)
    // if (res.data.code === '000000') {
    //     return res
    // }
}


let ajax = (method, url, params) =>{
    let httpDefault = {
        method: method,
        baseURL: baseURL,
        url: url,
        // `params` 是即将与请求一起发送的 URL 参数
        // `data` 是作为请求主体被发送的数据
        params: method === 'GET' || method === 'DELETE' ? params : null,
        data: method === 'POST' || method === 'PUT' ? JSON.stringify(params) : null,
        timeout: 10000,
        withCredentials:true        //请求带上cookie
    }
    if(method === 'POST'){
        httpDefault.headers = {
            'content-type':"application/json"
        }
    }
    return new Promise((resolve, reject)=>{
        Axios(httpDefault)
            .then((res) =>{
                resolve(res);
            })
            .catch(error =>{
                reject(error);
            })
    });
}

Vue.prototype.getHttp = (url,params) =>ajax("GET",url,params);
Vue.prototype.postHttp = (url,params) =>ajax("POST",url,params);
Vue.prototype.putHttp = (url,params) =>ajax("PUT",url,params);
Vue.prototype.delHttp = (url,params) =>ajax("DELETE",url,params);

export default ajax;






