import Vue from  "vue";
import Vuex from  "vuex";

let st = window.sessionStorage;

Vue.use(Vuex);

const state = {
   loginInfo:st.getItem('token'),
    routerList:JSON.parse(st.getItem("routerList")||"[]")
},mutations = {
    setLoginInfo(state,token){
        state.loginInfo = token;
        st.setItem('token', token);
    },
    logout(state,token){
        state.loginInfo = null;
        st.removeItem("token");
    },
    setRouterList(state,list){
        state.routerList = list;
        st.setItem("routerList",JSON.stringify(list));
    }
},getters = {
    loginInfo:state=>state.loginInfo,
    routerList:state=>state.routerList
}

export default new Vuex.Store({
    state,
    getters,
    mutations
});


